@import "reset";
@import "vars";
@import "mixins";
.wrapper {
    overflow: hidden;
    font-family: $font-primary;
    @include rfs(14); // Responsive Font-Size
    color: #514e4e;
}

.container {
    width: 98%; //custom padding    
    max-width: 1080px;
    margin: 0 auto;
}

button {
    background-color: transparent;
    border: none;
}

a {
    color: inherit;
    font-size: inherit;
    transition: all 0.2s ease;
}

// HEADER
.header-wrap {
    padding-bottom: 5px;
    padding-top: 15px;
}

.top-navbar-wrap {
    background: url("../img/menu_bg.jpg") repeat scroll center 0 transparent;
}

.top-navbar {
    min-height: 36px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    line-height: 2;
    color: #514e4e;
    @media only screen and (max-width: 1000px) {
        justify-content: flex-end;
    }
}

.navbar {
    display: flex;
    flex-wrap: wrap;
    flex-grow: 1;
    align-items: center;
    &__item {
        margin-right: 5%;
        flex-shrink: 0;
        color: inherit;
        &:hover {
            color: #8d46c2;
        }
    }
}

.work-time {
    margin-left: 10%;
    @media only screen and (max-width: 1000px) {
        span {
            display: block;
        }
    }
}

.top-content {
    margin: 20px 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    @media only screen and (max-width: 1000px) {
        justify-content: space-around;
    }
    @media only screen and (max-width: 380px) {
        flex-direction: column;
    }
}

.logo-box {
    position: relative;
    max-width: 200px;
    min-width: 120px;
    @media only screen and (max-width: 1000px) {
        width: 25%;
    }
    @media only screen and (max-width: 480px) {
        width: 35%;
        margin-bottom: 15px;
    }
    &:after {
        content: "Севастополь";
        position: absolute;
        top: -12px;
        right: 0;
        font-size: 12px;
    }
    img {
        width: 100%;
    }
}

.order-btn,
.price-btn {
    display: flex;
    align-items: center;
    padding-right: 1%;
    color: #514e4e;
    text-transform: uppercase;
    box-sizing: border-box;
    border-radius: 10px;
    border: 2px solid transparent;
    cursor: pointer;
    transition: all 0.3s ease;
    &:hover {
        border: 2px solid #8d46c2;
        i {
            border-color: transparent;
        }
    }
    i {
        width: 64px;
        height: 64px;
        border-radius: 50%;
    }
    span {
        padding-left: 3%;
        line-height: 1.5;
        text-align: left;
        flex-shrink: 0;
        font-weight: 700;
    }
}

.order-btn {
    @media only screen and (max-width: 1000px) {
        order: 4;
        margin-top: 15px;
        margin-left: 10%;
    }
    i {
        background: url("../img/icon--keyboard.png") center no-repeat;
        border: 2px solid #8d46c2;
    }
}

.price-btn {
    @media only screen and (max-width: 1000px) {
        order: 5;
        margin-top: 15px;
    }
    i {
        background: url("../img/icon--price.png") center no-repeat;
        border: 2px solid #76bf16;
    }
    &:hover {
        border: 2px solid #76bf16;
    }
}

.phones {
    line-height: 2;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    @media only screen and (max-width: 1000px) {
        flex-direction: row;
    }
    @media only screen and (max-width: 768px) {
        position: fixed;
        bottom: 0;
        left: 0;
        width: 100%;
        background-color: #fff;
        z-index: 100;
        box-shadow: 0 0px 10px 0 rgba(0, 0, 0, 0.2);
    }
}

.phones__number {
    flex-shrink: 0;
    padding: 0px 0px 3px 25px;
    @include rfs(18);
    font-weight: 700;
    background: url("../img/icon--phone.png") no-repeat left center;
    background-size: 20px 20px;
    &:hover {
        color: #8d46c2;
    }
    @media only screen and (max-width: 1000px) {
        margin-left: 10%;
    }
}

.phones__number--mts {
    background: url("../img/icon--mts.png") no-repeat left center;
    background-size: 20px 20px;
}

.phone-btn {
    padding: 7px 20px;
    color: #fff;
    font-size: 13px;
    font-weight: 700;
    text-transform: uppercase;
    background-color: #8d46c2;
    &:hover {
        opacity: 0.8;
    }
    @media only screen and (max-width: 1000px) {
        margin-left: 3%;
    }
}

// HERO SLIDER
.hero-slider {
    .slick-dots {
        bottom: 15px;
        z-index: 10;
    }
}

.hero-slider__item {
    position: relative;
    .image {
        margin: 0 auto;
    }
    .br {
        display: block;
    }
    .container {
        position: absolute;
        top: 10%;
        left: 0;
        right: 0;
        display: flex;
        align-items: center;
        justify-content: space-between;
        @media only screen and (max-width: 768px) {
            flex-direction: column;
            .br {
                display: inline-block;
            }
            .body_wrap {
                margin-bottom: 30px;
            }
            .form_wrap {
                margin-bottom: 30px;
            }
        }
    }
    .body_wrap .title {
        @include rfs(48);
        line-height: 1.2;
        text-transform: uppercase;
        color: #8d46c2;
        font-weight: 700;
        position: relative;
        padding-bottom: 20px;
        margin-bottom: 10px;
        &::after {
            content: '';
            display: block;
            position: absolute;
            left: 0;
            bottom: 0;
            width: 110px;
            height: 5px;
            background: #8d46c2;
        }
    }
    .body {
        @include rfs(36);
        line-height: 1.3;
        text-transform: uppercase;
        font-weight: 700;
    }
}


/*form*/

.form_2 {
    max-width: 300px;
    padding: 15px 30px;
    text-align: center;
    background-color: #ffffff;
    box-sizing: border-box;
}

.form_2 .title {
    @include rfs(20);
    padding: 15px 0;
    text-transform: uppercase;
    font-weight: 700;
    line-height: 1.3;
}

.form_2 input {
    margin-bottom: 15px;
}

.form-control {
    display: block;
    width: 100%;
    height: 34px;
    padding: 6px 12px;
    box-sizing: border-box;
    @include rfs(14);
    line-height: 1.42857143;
    color: #555555;
    background-color: #ffffff;
    background-image: none;
    border: 1px solid #cccccc;
    -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
    box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
    -webkit-transition: border-color ease-in-out .15s, -webkit-box-shadow ease-in-out .15s;
    -o-transition: border-color ease-in-out .15s, box-shadow ease-in-out .15s;
    transition: border-color ease-in-out .15s, box-shadow ease-in-out .15s;
}

.form_submit {
    padding-right: 20px;
    padding-left: 20px;
    @include rfs(16);
    text-transform: uppercase;
    font-weight: 700;
    color: #fff;
    padding: 6.5px 25px;
    background: #ee008c;
    border: none;
    -webkit-transition: 0.5s;
    -o-transition: 0.5s;
    transition: 0.5s;
}

.form_submit:hover {
    opacity: 0.8;
}

// common styles
// subtitle
.subtitle {
    position: relative;
    padding: 30px 0 15px 0;
    margin-bottom: 30px;
    @include rfs(24);
    text-transform: uppercase;
    font-weight: 700;
    &:after {
        content: '';
        display: block;
        position: absolute;
        bottom: 0;
        left: 0;
        background: #76bf16;
        width: 110px;
        height: 2px;
    }
}

.body__text {
    @include rfs(16);
    line-height: 1.3;
}

// COMPANY
.company {
    .title {
        margin: 30px 0;
        line-height: 1.3;
        text-align: center;
        @include rfs(36);
        color: #8d46c2;
        text-transform: uppercase;
        font-weight: 700;
        padding-bottom: 10px;
    }
    &__content {
        padding-bottom: 10px;
        display: flex;
        justify-content: space-between;
        border-bottom: 1px solid #cccccc;
        @media only screen and (max-width: 1000px) {
            flex-direction: column;
            justify-content: center;
        }
        span {
            font-weight: 700;
        }
    }
    &__text {
        max-width: 40%;
        @media only screen and (max-width: 1000px) {
            max-width: 100%;
        }
    }
    &__authorized {
        margin: 30px 0;
        @media only screen and (max-width: 1000px) {
            margin: 15px 0 5px 0;
        }
    }
    .company__more {
        @media only screen and (max-width: 1000px) {
            display: block;
            margin-right: 15px;
            text-align: right;
        }
    }
    .company__video {
        @media only screen and (max-width: 1000px) {
            margin-top: 15px;
            text-align: center;
        }
        @media only screen and (max-width: 560px) {
            iframe {
                width: 100%;
                height: 315px;
            }
        }
    }
    // how we work
    &__work-items {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        align-items: center;
        @media only screen and (max-width: 768px) {
            justify-content: space-around;
        }
        @media only screen and (max-width: 480px) {
            flex-direction: column;
        }
    }
    &__work-item {
        position: relative;
        padding-right: 7%;
        padding-left: 50px;
        margin-bottom: 45px;
        @media only screen and (max-width: 480px) {
            padding-right: 15%;
        }
        &:after {
            content: '';
            display: block;
            position: absolute;
            right: 17px;
            top: 50%;
            margin-top: -32px;
            width: 22px;
            background: url("../img/work--next_arrow.png") no-repeat scroll 0 0 transparent;
            height: 60px;
        }
    }
    .company__work-item--1 {
        background: url("../img/work--pencil.png") no-repeat scroll 0 0 transparent;
    }
    .company__work-item--2 {
        background: url("../img/work--house.png") no-repeat scroll 0 0 transparent;
    }
    .company__work-item--3 {
        background: url("../img/work--window.png") no-repeat scroll 0 0 transparent;
    }
    .company__work-item--4 {
        background: url("../img/work--key.png") no-repeat scroll 0 0 transparent;
    }
    &__work-caption {
        @include rfs(18);
        font-weight: 700;
        line-height: 1.3;
    }
}

// BLOCK CHOICE 
.choice {
    background-color: #edf2f6;
    padding-bottom: 15px;
    .choice__row {
        display: flex;
        margin-bottom: 30px;
        img {
            max-height: 85px;
            @media only screen and (max-width: 768px) {
                max-height: 60px;
            }
        }
        .body__text {
            font-weight: 300;
            line-height: 1.5;
            ul {
                display: flex;
                flex-wrap: wrap;
                li {
                    width: 50%;
                    padding-right: 5%;
                    box-sizing: border-box;
                    @media only screen and (max-width: 768px) {
                        width: 100%;
                    }
                }
            }
        }
    }
    .choice__icon {
        margin-right: 2%;
    }
    .choice__title {
        @include rfs(22);
        font-weight: 700;
        line-height: 4;
        color: #8d46c2;
    }
    .choice__order {
        text-align: center;
        .phone-btn {
            display: inline-block;
            height: 40px;
            line-height: 40px;
            background-color: #76bf16;
        }
    }
}

// BENEFITS
.benefits {
    margin-top: 30px;
    .benefits__items {
        width: 100%;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        align-items: center;
    }
    .benefits__item {
        width: 30%;
        text-align: center;
        margin-bottom: 60px;
        @include rfs(20);
        @media only screen and (max-width: 640px) {
            width: 50%;
        }
        @media only screen and (max-width: 479px) {
            width: 100%;
        }
        span {
            font-weight: 600;
        }
    }
    .benefits__caption {
        margin-top: 15px;
        line-height: 1.3;
    }
    .benefits__order {
        padding: 15px 30px;
        display: inline-block;
        border: 2px solid #76bf16;
        color: #76bf16;
        border-radius: 5px;
        transition: all 0.3 ease;
        &:hover {
            background-color: #76bf16;
            color: #fff;
        }
    }
}

// GALLERY
.gallery {
    padding-bottom: 30px;
    background-color: #edf2f6;
    .gallery__group {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;
    }
    .gallery__item {
        position: relative;
        margin-bottom: 30px;
        overflow: hidden;
    }
    .gallery__item-caption {
        position: absolute;
        left: 0;
        bottom: 2px;
        width: 100%;
        padding-top: 15px;
        padding-bottom: 15px;
        opacity: 0;
        text-align: center;
        color: #fff;
        background-color: rgba(0, 0, 0, 0.5);
        transition: opacity 0.3s ease;
    }
    .gallery__item:hover .gallery__item-caption {
        opacity: 1;
    }
}

.metering {
    height: 565px;
    width: 100%;
    background-image: url("../img/metering__bg.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    @media only screen and (max-width: 768px) {
        padding-top: 30%;
        background-image: url("../img/metering__bg--768.jpg");
        background-size: contain;
        background-position: top;
    }
    @media only screen and (max-width: 640px) {
        padding-top: 20%;
    }
    @media only screen and (max-width: 480px) {
        padding-top: 10%;
    }
    @media only screen and (max-width: 380px) {
        padding-top: 0%;
    }
    .metering-wrap {
        height: 565px;
        display: flex;
        align-items: center;
        justify-content: flex-end;
        @media only screen and (max-width: 768px) {
            justify-content: center;
            align-items: flex-end;
        }
        .form_2 {
            position: static;
            @media only screen and (max-width: 768px) {
                border: 1px solid #ccc;
            }
        }
    }
}

// PAYING
.paying__group {
    margin-top: 30px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    @media only screen and (max-width: 768px) {
        justify-content: space-around;
    }
}

.paying__item {
    width: 163px;
    height: 91px;
    margin-bottom: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 16px;
    line-height: 1.3;
    box-sizing: border-box;
    border: 1px solid #e5e5e5;
    position: relative;
    font-weight: 700;
    span {
        display: block;
    }
    @media only screen and (max-width: 768px) {
        margin-left: 5%;
        margin-right: 5%;
    }
    @media only screen and (max-width: 640px) {
        width: 80%;
        height: auto;
        line-height: 2;
        span {
            display: inline-block;
        }
    }
    @media only screen and (max-width: 480px) {
        width: 100%;
    }
    &:after {
        content: '';
        display: block;
        position: absolute;
        bottom: -14px;
        width: 167px;
        height: 13px;
        background: url("../img/shadow.png") no-repeat scroll 0 0 transparent;
    }
    img {
        width: 100%;
    }
}

// CERTIFICATES
.cetificates {
    margin-top: 30px;
    .certificates__slider {
        margin: 0 auto;
        max-width: 850px;
    }
    .certificates__slide {
        margin-right: 15px;
        margin-left: 15px;
        display: flex;
        justify-content: center;
        img {
            border: 1px solid #ccc;
            max-height: 360px;
        }
    }
}

.prices {
    margin-top: 30px;
    .prices__group {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        @media only screen and (max-width: 768px) {
            justify-content: center;
        }
    }
    .prices__title {
        margin-bottom: 30px;
        @include rfs(25);
        text-align: center;
        font-weight: 700;
        text-transform: uppercase;
    }
    .prices__item {
        position: relative;
        width: 23%;
        padding: 30px;
        margin-bottom: 30px;
        text-align: center;
        border: 2px solid #ccc;
        border-radius: 5px;
        @media only screen and (max-width: 768px) {
            width: 60%;
        }
        @media only screen and (max-width: 768px) {
            width: 100%;
        }
        &:after {
            content: '';
            display: block;
            position: absolute;
            bottom: -15px;
            left: 0;
            width: 100%;
            height: 13px;
            background: url("../img/shadow.png") no-repeat scroll center 0 transparent;
            opacity: 0.5;
        }
    }
    .prices__btn {
        display: inline-block;
        padding: 10px 20px;
        @include rfs(16);
        text-transform: uppercase;
        font-weight: 700;
        color: #76bf16;
        border: 2px solid #76bf16;
        transition: all 0.3s ease;
        &:hover {
            color: #fff;
            background-color: #76bf16;
        }
    }
    .prices__item--1 {
        &:hover {
            border-color: #76bf16;
        }
        .prices__title {
            color: #76bf16;
        }
    }
    .prices__item--2 {
        &:hover {
            border-color: #8d46c2;
        }
        .prices__title {
            color: #8d46c2;
        }
    }
    .prices__item--3 {
        &:hover {
            border-color: #ee008c;
        }
        .prices__title {
            color: #ee008c;
        }
    }
    .prices__description {
        margin-top: 30px;
        margin-bottom: 30px;
        line-height: 1.3;
        text-align: left;
    }
    .prices__price {
        text-align: center;
        @include rfs(20);
        font-weight: 700;
        margin-bottom: 30px;
        span {
            display: inline-block;
            margin-left: 5px;
            margin-right: 5px;
            @include rfs(30);
        }
    }
}

// REVIEWS
.reviews {
    padding-top: 15px;
    padding-bottom: 60px;
    background-color: #edf2f6;
    .reviews__slider {
        max-width: 640px;
        margin: 0 auto;
    }
    .reviews__slide {
        margin: 15px;
        padding: 30px;
        display: flex;
        background-color: #fff;
        border: 1px solid #cccc;
        box-shadow: 0px 0px 4px 1px rgba(3, 3, 3, 0.15);
        @media only screen and (max-width: 640px) {
            flex-direction: column;
            align-items: center;
        }
        img {
            width: 210px;
            height: 140px;
            @media only screen and (max-width: 640px) {
                width: 90%;
                height: auto;
            }
        }
    }
    .reviews__text {
        margin-right: 15px;
        line-height: 1.3;
        @media only screen and (max-width: 640px) {
            margin-bottom: 30px;
        }
        span {
            margin-top: 15px;
            display: block;
            font-weight: 600;
            font-style: italic;
        }
    }
}

// RULES
.rules {
    .rules__wrap {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;
    }
    .rules__item {
        width: 43%;
        margin-bottom: 60px;
        padding-left: 60px;
        @media only screen and (max-width: 968px) {
            width: 100%;
        }
    }
    .rules__number {}
    .rules__title {
        position: relative;
        display: inline-block;
        @include rfs(18);
        line-height: 1.1;
        padding-top: 15px;
        margin-bottom: 20px;
        color: #8d46c2;
        text-transform: uppercase;
        font-weight: 700;
        &:before {
            content: "1";
            position: absolute;
            top: 0;
            left: -60px;
            display: inline-block;
            height: 45px;
            width: 45px;
            line-height: 45px;
            font-size: 18px;
            font-weight: 700;
            color: #8d46c2;
            text-align: center;
            border: 2px solid #8d46c2;
            border-radius: 50%;
        }
    }
    .rules__title--2 {
        &:before {
            content: "2";
        }
    }
    .rules__title--3 {
        &:before {
            content: "3";
        }
    }
    .rules__title--4 {
        &:before {
            content: "4";
        }
    }
    .rules__title--5 {
        &:before {
            content: "5";
        }
    }
    .rules__title--6 {
        &:before {
            content: "6";
        }
    }
    .rules__text {
        @include rfs(16);
        line-height: 1.5;
        color: #514e4e;
    }
}

// FOOTER
.footer__wrap {
    text-align: center;
    background: url("../img/footer_bg.png") repeat scroll 0 0 transparent;
    padding: 30px;
    @include rfs(13);
    font-weight: 300;
    color: #fff;
}

// MODAL POPUP
.mfp-content {
    margin: 0 auto;
    max-width: 480px;
    padding: 0;
}

.modal-popup {
    max-width: 100%;
    font-family: $font-primary;
    color: #514e4e;
    text-align: left;
    .modal__title {
        margin-top: 15px;
        margin-bottom: 15px;
        @include rfs(24);
        font-weight: 700;
        text-transform: uppercase;
    }
    .modal__subtitle {
        @include rfs(14);
        margin-bottom: 15px;
        line-height: 1.1;
    }
    .mfp-close {
        color: #514e4e;
    }
}